@import './styles/reset.css';
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@700;900&display=swap');

:root {
	/* Colors */
	--pink: #FF70D9;
	--black: #000000;
	--dark-blue: #304571;
	--white: #fff;
	--yellow: #EBFF00;

  /* Btn */
	--btn-bg: var(--yellow);
	--btn-border: var(--yellow);
  --btn-text: var(--yellow);

	/* Nav */
	--nav-bg: var(--black);
	--nav-text: var(--white);

	/* Header */
	--header-bg: var(--black);
	--header-text: var(--white);

	/* Footer */
	--footer-bg: var(--black);
	--footer-text: var(--yellow);

}

@mixin extraBold {
  font-weight: 900;
}

html,
body {
	min-height: 100vh;
	font-family: 'Lato', sans-serif;
  font-size: 1rem;
  background: var(--dark-blue);
}

h1, .h1 {
  @include extraBold;

  font-size: 4.5rem;
  margin: 0px 0px 1rem;
}

h2, .h2 {
  @include extraBold;

  font-size: 2rem;
  margin: 0px 0px 1rem;
}

h3, .h3 {
  @include extraBold;

  font-size: 1.5rem;
  margin: 0px 0px 1rem;
}

#root {
  height: 100vh;
}

.App {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.container {
	margin: 0 auto;
	padding: 0 24px;
	max-width: 1200px;
}

.none {
	display: none !important;
}

.tx_color {
  &-yellow {
    color: var(--yellow);
  }

  &-pink {
    color: var(--pink);
  }
}

@mixin animationAll {
  -webkit-transition: all 0.3s ease;;
  -moz-transition: all 0.3s ease;;
  -o-transition: all 0.3s ease;;
  transition: all 0.3s ease;
}

@mixin opacityOn($num) {
  opacity: $num;
}

/* Link */
.link-info {
  @include animationAll;
  @include extraBold;

  color: var(--yellow);
  text-decoration: none;

  &:hover {
    @include opacityOn(0.65);
  }
}

/* Btn */
.btn {
  @include animationAll;

	display: inline-block;
  box-sizing: border-box;
	padding: 0.4rem 2.5rem;
	border-radius: 100px;
	background-color: var(--btn-bg);
  border: 2px solid var(--btn-border);
  line-height: 1;
	color: var(--black);
	letter-spacing: 0.15px;
	font-size: 1rem;
  
  &:hover {
    @include opacityOn(0.65);
  }
}

.btn-outline {
  @extend .btn;

  background: transparent;
  color: var(--btn-text);
}

/*mobile nav*/
#menu__toggle {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

/*nav*/
.nav {
  @include animationAll;

  flex: 1 0 auto;
  background: var(--header-bg);
  height: 80px;

  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 100%;
  }

  &__mobile {
    display: none;
  }

  &__logo {
    flex-shrink: 1;
    min-width: 71px;
  }

  &-box {
    display: flex;
    flex-shrink: 2;
  }

  &-list {
    display: flex;

    &__item {
      margin: 0px 0px 0px 1.5rem;
      font-size: 1.13rem;
      display: flex;
      align-items: flex-start;
  
      &:first-child {
        margin: 0px;

        a {
          padding-left: 0px
        }
      }
  
      a {
        color: var(--white);
        text-decoration: none;
        padding: 0px 1rem;
        flex-shrink: 0;

        &:hover {
          background-image: linear-gradient(90deg, var(--yellow), var(--pink));
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
        }
      }

      &.cart {
        img, svg {
          width: 100%;
        }

        a:hover {
          @include opacityOn(0.6);
          
          img, svg {
            path {
              fill: url(#gradientLine);
            }
          }
        }
      }
    }
  }
}

/*header*/
.header {
  @include animationAll;

  flex: 1 1 auto;
  color: var(--white);
  background-image: linear-gradient(to left, rgba(0,0,0,0) 0%,rgba(15,20,32,0.79) 100%), image-set("./img/background/header_base.png" 1x, "./img/background/header_base_x2.png" 2x, "./img/background/header_base_x3.png" 3x),;
  background-position: 0px center;
  background-repeat: no-repeat;
  background-size: cover;

  .container {
    overflow: hidden;
  }

  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 3.75rem 0px;

    & &-info {
      max-width: 700px;

      h1, .h1 {
        line-height: 1;
        margin: 0px 0px 1rem;
      }

      p {
        @include extraBold;

        font-size: 1.35rem;
      }
    }

    &-img img {
      @include animationAll;

      position: relative;
      min-width: 300px;
      right: -20px;
    }
    
    & &-btn {
      margin: 1.4rem 0px 0px;

      .btn {
        margin: 0px 0px 0px 1rem;

        &:first-child {
          margin-left: 0px;
        }
      }
    }
  }
}

/* main */
.main {
  padding: 3.75rem 0px;
  color: var(--white);
}

/* post */
.post {
  &-list {
    display: grid;
    justify-content:space-between;
    gap: 2.5rem;
    grid-template-columns: repeat(2, 1fr);
    margin: 2.5rem 0px;

    &__item {
      min-height: 350px;
      box-sizing: border-box;
      border-radius: 9px;
      overflow: hidden;
      background-color: var(--black);
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      padding: 2.25rem;  
      display: flex;
      justify-content: flex-end;
      flex-direction: column;
      align-items: start;
    }
  }

  p {
    @include extraBold;

    font-size: 1.5rem;
  }

  & &__title {
    font-size: 2.85rem;
    line-height: 1.05;
    margin: 0px 0px 1rem;
  }

  &__btn {
    margin: 1.35rem 0px 0px;
  }
}

/* subinfo */
.subinfo {
  font-size: 1rem;
  padding: 1.25rem 0px 0px;

  .link-info {
    display: inline-block;
    margin: 1.15rem 0px 0px;
  }

  p {
    line-height: 1.3;
    opacity: 0.6;
    margin: 1rem 0px 0px;
  }
}

/* footer */
.footer {
  flex: 0 0 auto;
  background: var(--header-bg);
  color: var(--footer-text);
  text-align: center; 
  min-height: 127px;

  p {
    font-size: 1.15rem;
    font-weight: 900;
  }
}

@media (min-width:769px) {
  .header__content {
    & &-info p {
      font-size: 2rem;
    }
  }

  .post {
    &__description {
      width: 60%;
      min-width: 300px;   
    }

    &-list {
      &__item {
        &:first-child,  &:last-child {
          grid-column: 1 / 3;
  
          .post__title {
            font-size: 4.65rem;
            margin: 0px 0px 1.35rem;
          }
        }
      }
    }
  }

  .subinfo {
    font-size: 1.25rem;
  }

  .btn, .btn-outline {
    font-size: 1.375rem;
    padding: 0.85rem 2.5rem;
  }
}

@media (max-width:768px) {
  h1, .h1 {
    font-size: 3rem;
  }

  h2, .h2 {
    font-size: 1.5rem;
  }

  h3, .h3 {
    font-size: 1.2rem;
  }

  .nav {
    &__logo {
      width: 71px;
    }

    &-list__item {
        &.cart a {
          padding-left: 0px;
      }
    }
  }

  .header {
    background-position: right bottom;

    &__content {
      margin-top: 1.6rem;

      &-info {
        width: 72%;
        flex-shrink: 0;
      }

      &-img {
        flex-shrink: 1;
      }
    }
  }

  .main {
    padding: 3.15rem 0px;
  }

  .post {
    p {
      font-size: 1rem;
    }

    & &__title {
      font-size: 2.45rem;
    }

    &-list {
      gap: 1.85rem;
      margin: 1.85rem 0px;

      &__item {
        padding: 1.5rem;
      }
    }
  }
}

@media (min-width: 361px) {
  .header {
    .btn, .btn-outline {
      padding: 0.85rem 2.5rem;
    }
  }
}

@media (max-width: 540px) {
  .post {
    &-list {
      grid-template-columns: repeat(1, 1fr);
    }
  }
}

@media (max-width: 768px) and (min-width: 361px) {
  .header, .main {
    border-left: 1px solid var(--black);
    border-right: 1px solid var(--black);
  }
}
@media (max-width: 480px) and (min-width: 361px) {
  .nav {
    &-list {
      &__item {
        margin: 0px 0px 0px 1rem;

        a {
          padding: 0px 0.5rem;
        }
      }
    }
  }
}

@media (max-width: 360px) {
  h1, .h1 {
    font-size: 2.25rem;
  }

  h2, .h2 {
    font-size: 1.2rem;
  }

  h3, .h3 {
    font-size: 1rem;
  }

  .nav {
    height: 48px;
     &-list {
      &__mobile {
        display: block;
        position: absolute;
        z-index: -1;
        visibility: hidden;
        transform: translateX(-100%);
        top: 48px;
        right: 0px;
        width: 100%;
        height: auto;
        margin: 0px;
        padding: 0px;
        list-style: none;
        background-color: var(--black); 
        transition-duration: .25s;
  
        & > li {
          border-top: 1px solid var(--yellow);
        }
      }

      &__item {
        margin: 0px;
        display: block;
        padding: 12px 24px;
        text-align: center;
        text-decoration: none;
        transition-duration: .25s;
        text-align: center;
  
        a {
          padding: 0.75rem 0px;
          display: block;
          cursor: pointer;
          width: 100%;
  
        }
  
        &.cart {
          img, svg {
            
            width: auto;
            height: auto
          }
        }
  
        a {
          padding: 0px
        }
      }
    }
  }

  .menu {
    &__box {
      display: block;
    }
  }

  /*mobile button nav*/
  #menu__toggle {
    &:checked {
      & ~ .menu__btn > span {
        transform: rotate(45deg);

        &::before {
          top: 0;
          transform: rotate(0);
        }

        &::after {
          top: 0;
          transform: rotate(90deg);
        }
      }

      & ~ .nav-list {
        z-index: 1;
        visibility: visible;
        transform: translateX(0);
      }
    }
  }
  
  .menu__btn {
    display: flex;
    align-items: center;
    position: absolute;
    top: 11px;
    right: 20px;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 1;

    & > span, & > span::before, & > span::after {
      display: block;
      position: absolute;
      width: 100%;
      height: 2px;
      background-color: var(--white);
    
      transition-duration: .25s;
    }

    & > span::before {
      content: '';
      top: -8px;
    }

    & > span::after {
      content: '';
      top: 8px;
    }
  }

  .header {
    background-image: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(15,20,32,0.79) 100%), image-set("./img/background/header_base.png" 1x, "./img/background/header_base_x2.png" 2x, "./img/background/header_base_x3.png" 3x),;
    background-position: 60% bottom;

    &__content {
      margin: 0px 0px 2.5rem;
      flex-direction: column-reverse;

      & &-info {
        width: 100%;
        text-align: center;

        p {
          font-size: 1rem;
        }
      }

      &-img {
        margin-top: -107px;

        img {
          right: 0px;
        }
      }
    }
  }
  
  .container {
    padding: 0px 20px;
  }
  .subinfo {
    font-size: 0.85rem;
  }
}
